#root {
   
	color: #888;
  }
.App {
	font-family: "Karla", sans-serif;
	/* max-width: 1200px; */
	width: 95vw;
	margin: 2% auto;
	/* box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1); */
	border-radius: 10px;
	background-color: #272827;
}

/* Header Component */
header {
	display: flex;
	align-items: center;
	height: 5em;
	
	background: linear-gradient(90.41deg, #391f8d 1.14%, #A818DA 100%);	
	
	padding: 1.2em;
	border-radius: 10px;
}

.header-image {
	height: 100%;
	margin-right: 1em;
}

.header-title {
	font-size: 1.25rem;
	margin-right: auto;
}

/* Header Component */

/* Meme Component */
main {
	padding: 5%;
	display: flex;
	flex-direction: column;
}

.form {
	display: grid;
	grid-template: 40px 40px / 1fr 1fr;
	text-decoration: white;
	gap: 1em;
}

.form-input {
	font-family: "Karla", sans-serif;
	border-radius: 5px;
	color: white ;
	background-color: rgb(43, 42, 42);
	border: 1px solid #D5D4D8;
	text-indent: 5px;
}

.form-button {
	grid-column: 1 / -1;
	background: linear-gradient(90.41deg, #391f8d 1.14%, #A818DA 100%);
	font-family: "Karla", sans-serif;
	border-radius: 5px;
	font-weight: 500;
	color: #f1edea;
	border: none;
	cursor: pointer;
}

.meme {
	position: relative;
	margin-top: 2em;
	align-self: center;
}

.meme-image {
	max-width: 100%;
}

.meme-text {
	position: absolute;
	width: 80%;
	text-align: center;
	left: 50%;
	transform: translateX(-50%);
	margin: 15px 0;
	padding: 0 5px;
	font-family: impact, sans-serif;
	font-size: 2em;
	text-transform: uppercase;
	color: white;
	letter-spacing: 1px;
	text-shadow:
		2px 2px 0 #000,
		-2px -2px 0 #000,
		2px -2px 0 #000,
		-2px 2px 0 #000,
		0 2px 0 #000,
		2px 0 0 #000,
		0 -2px 0 #000,
		-2px 0 0 #000,
		2px 2px 5px #000;
}

.bottom {
	bottom: 0;
}

.top {
	top: 0;
}

/* Meme Component */

/* Footer Component */
footer {
	text-align: center;
	font-size: 1em;
	color: #c6c5c4;
	background-color: #272827;
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px;
	
	
}

footer p {
	padding: 0.7em 0;
	margin: 0;
}

footer a {
	text-decoration: none;
	color: #dde5e5;
}

footer a:hover {
	filter: opacity(40%);
}

/* Footer Component */


@media only screen and (max-width: 600px) {
	.meme-text {
		font-size: 1.55em;
	}
}